<template>
    <div class="circle">
      <svg class="circle-svg" :width="width" :height="height" >
        <circle class="circle-background" :r="radius" :cx="center" :cy="center" fill="transparent" />
        <circle class="circle-progress" :r="radius" :cx="center" :cy="center" fill="transparent" :style="circleProgressStyle" />
        <text :x="center" :y="center" class="circle-text" text-anchor="middle" dy=".3em" :transform="`rotate(90, ${center}, ${center})`">{{percentage}}</text>
      </svg>
    </div>
  </template>
  
  <script>
  export default {
    props: ['percentage', 'width', 'height'],
    computed: {
      // 
      radius() {
        //  width  height  width 
        return this.width / 2 - 10; // 
      },
      center() {
        return this.width / 2;
      },
      // 
      circleProgressStyle() {
        const circumference = 2 * Math.PI * this.radius;
        const offset = ((100 - this.percentage) / 100) * circumference;
        return {
          strokeDasharray: `${circumference} ${circumference}`,
          strokeDashoffset: offset,
        };
      },
    },
  };
  </script>
  
  <style scoped>
  .circle-svg {
    transform: rotate(-90deg);
  }
  
  .circle-background {
    stroke: #e6e6e6;
    stroke-width: 5;
  }
  
  .circle-progress {
    stroke: #05b8f4;
    stroke-width: 5;
    stroke-linecap: round;
  }
  
  .circle-text {
    font-size: 1em;
    fill: #333;
  }
  </style>
  