<script>
// @ is an alias to /src
//import http from '../api/http'
import moment from 'moment'
import axios from 'axios'
import circlePercent from '@/model/circlePercent.vue';

//const apiBaseUrl = '/api';
//axios.defaults.withCredentials = true;
const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
const cookie_re = "";
// const http = axios.create({
//   baseURL: apiBaseUrl,
//   timeout: 5000,
//   headers:{
//     Cookie: cookie_re,
//   }
// });
// http.defaults.withCredentials = true;
axios.defaults.withCredentials = true

export default {
  components: { circlePercent },
  name: 'HomeView',
  data(){
    return{
      width:0,
      loadUpload:false,
      percentChange:0,
      categoryType:{AudioFeature:false,DenseFlow:false,ImageBackground:false,FacialLandmark:false,FacialDescription:false,FeatureAudio:false,FeatureImage:false,FeatureText:false,IsAnalysis:false,IllustrationText:false,IllustrationAudio:false,IllustrationImage:false},
      checkCategoryType:{AudioFeature:false,DenseFlow:false,ImageBackground:false,FacialLandmark:false,FacialDescription:false,FeatureAudio:false,FeatureImage:false,FeatureText:false,IsAnalysis:false,IllustrationText:false,IllustrationAudio:false,IllustrationImage:false},
      table1:[
      ],
      table1Org:"",
      page:{
        pageTotal:0,
        pageNum:1,
      },
      table2:[
      ],
      progressText:"",
      showTable:{tb1:false,tb2:false,tb3:false,tb4:false},
      percent:0,
      showFile:false,
      showPercent:false,
      uploadPercent:0,
      dataIsLoading:false,
      dataLoadingText:"",
      apiBaseUrl:'https://www.multimodal-emotion-converter.com',
      //apiBaseUrl:'/api',
      //cookie:""
      video1:'',
      video2:'',
      loadpercent:0,
    }
  },
  mounted(){
    this.getCookie();
    this.width = this.$refs.mydiv.offsetWidth;
    window.addEventListener('resize', this.updateWidth);
    console.log(this.width);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.updateWidth);
  },
  methods:{
    updateWidth() {
      this.width = this.$refs.mydiv.offsetWidth;
    },
    submitForm(){
      if(this.loadpercent == 100){
        this.loadpercent = 0;
      }
      this.checkCategoryType = {...this.categoryType};
      this.dataIsLoading = true;
      this.showTable.tb1 = false;
      this.showTable.tb2 = false;
      this.showTable.tb3 = false;
      this.showTable.tb4 = false;
      this.percent = 0;
      this.percentChange = 0;
      

      axios.get(`${apiBaseUrl}/progress/check`,{withCredentials:true}).then(res=>{
      //http.get('/progress/check').then(res=>{
        if(res.data){
          if(res.data.status ==200){
            this.dataLoadingText = 'Calculating progress '+res.data.progress;
            if(res.data.progress >= 100){
              this.loadpercent =100;
              console.log(res)
              this.dataIsLoading = true
              this.checkForm();
            }
            else{
              this.loadpercent = res.data.progress;
              setInterval( this.submitForm(),2000);

            }
          }
          if(res.data.status ==500){
            this.$message.error(res.data.progress)
            this.showTable.tb1 = false;
            this.showTable.tb2 = false;
            this.showTable.tb3 = false;
            this.showTable.tb4 = false;
            return;
          }
        }
      }).catch(err=>{
        this.$message.error(err);
        console.log('/progress/check',err);
        this.showTable.tb1 = false;
        this.showTable.tb2 = false;
        this.showTable.tb3 = false;
        this.showTable.tb4 = false;
        this.dataIsLoading = false;
      })
    },
    checkForm(){
      if(this.checkCategoryType.IsAnalysis){
        this.showTable.tb1 = true;
      }
      if(this.checkCategoryType.AudioFeature || this.checkCategoryType.FacialDescription || this.checkCategoryType.DenseFlow || this.checkCategoryType.FacialLandmark || this.checkCategoryType.ImageBackground){
        this.showTable.tb2 = true;
      }
      if(this.checkCategoryType.FeatureAudio || this.checkCategoryType.FeatureImage || this.checkCategoryType.FeatureText){
        this.showTable.tb3 = true;
      }
      if(this.checkCategoryType.IllustrationAudio || this.checkCategoryType.IllustrationImage || this.checkCategoryType.IllustrationText){
        this.showTable.tb4 = true;
      }
      this.page.pageNum = 1;
      //data = {fileType:'outcome'}
      axios.get(`${apiBaseUrl}/data/query`,{withCredentials:true,params:{
        fileType:'outcome'}}).then(
      //http.get('/data/query',{data}).then(
        res=>{
          if(res.data){
            let d = res.data.score/7*100;
            d = Number(Number(d).toFixed(2));
            this.percentChange = d;
            this.progressText = res.data.category;
          }
        }).catch(err=>{
          this.$message.error(err);
          console.log(err);
      });

      axios.get(`${apiBaseUrl}/data/query`,{withCredentials:true,params:{
       fileType:'variable'
      }}).then(res=>{
      //http.get('/data/query',{fileType:'variable'}).then(res=>{
        if(res.data){
          let title = Object.keys(res.data);
          let d=[];
          let nowDate = new Date();
          for(let i in title){
          let innerTitle = Object.keys(res.data[title[i]])
          let innerValue = Object.values(res.data[title[i]])
          title[i] = title[i].slice(0,-5);
          console.log(title[i])
          if(this.categoryType[title[i]]){
            for(let j in innerTitle){
              let mes={Variable:'',Categroy:'',CalculateTime:'',Value:''}
              mes.Categroy = title[i];
              mes.CalculateTime = moment(new Date().getTime()).format("YYYY-MM-DD HH:mm:ss");
              mes.Variable = innerTitle[j];
              mes.Value = innerValue[j];
              d.push(mes)
            }
          }
            //console.log(d)
            this.table1Org = d;
            this.queryTable1();
          }
        }
      }).catch(err=>{
        this.$message.error(err);
      });

      axios.get(`${apiBaseUrl}/data/query`,{withCredentials:true,params:{
        fileType:'feature'
      }}).then(res=>{
      //http.get('/data/query',{fileType:'feature'}).then(res=>{
        if(res.data){
          let d = [];
          let title = Object.keys(res.data)
          for(let i in title){
            if(title[i]=='audio'){
              if(this.checkCategoryType.FeatureAudio){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
            if(title[i]=='vision'){
              if(this.checkCategoryType.FeatureImage){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
            if(title[i]=='text'){
              if(this.checkCategoryType.FeatureText){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
          }
          this.table2 = d;
        }
      }).catch(err=>{
        this.$message.error(err);
      });

      axios.get(`${apiBaseUrl}/views/video`,{withCredentials:true})
      .then(res=>{
        if(res.data){
          this.video1 = apiBaseUrl + '/assets/'+ res.data.dense_dir;
          this.video2 = apiBaseUrl + '/assets/' + res.data.face_dir;
          console.log(this.video1,this.video2);
        }
      }).catch(err=>{
        this.$message.error(err);
      })

      if(this.video1){
        axios.get(`${apiBaseUrl}${this.video1}`).then(res=>{
          console.log(res.data)
        })
      }
    },
    format(percentage){
          if(percentage<30)return "Negative";
          if(percentage>=30 && percentage<70)return "Neutral";
          if(percentage>=70)return "Positive";
    },
    sendVideo(param){
      this.showFile = false;
      this.loadUpload = true;
      const formData = new FormData();
      formData.append('file',param.file);
      const url = `${apiBaseUrl}/video/upload`;
      
      axios.post(url,formData,{withCredentials: "true",}).then(res=>{
      //http.post('/video/upload',formData).then(res=>{
        //console.log(res.data.status)
        if(res.data.status == 200){
          console.log("upload success");
          this.showFile = true;
          this.loadUpload = false;
          this.$message.success("Upload Success")
        }
        else{
          console.log(res.data.status);
          this.loadUpload = false;
          this.$message.error(res.data.message)
        }
      }).catch(error=>{
        console.log("upload faild");
        this.loadUpload = false;
        this.$message.error(error)
      });
    },
    getCookie(){
      axios.get(`${apiBaseUrl}/video/submit`,{withCredentials: "true",})
      //http.get('/video/submit')
      .then(res=>{
        console.log("submit success");
        // const setCookieHeader = response.headers['Set-Cookie'];
        // localStorage.setItem('customCookie', setCookieHeader);
        // cookie_re = setCookieHeader;
        // console.log("cookie:",cookie_re);
      }).catch(err=>{
        //this.$message.error(err);
        console.log("submit faild",err)
      })
    },
    handleCurrentChange(pagenation){
      this.page.pageNum = pagenation;
      this.queryTable1()
      console.log(pagenation)
    },
    queryTable1(){
      this.table1 = []
      this.page.pageTotal = this.table1Org.length;
      for(let i=(this.page.pageNum-1)*(5);i<(this.page.pageNum-1)*5+5;i++){
        if(this.table1Org[i])
          this.table1.push(this.table1Org[i]);
      }
      this.dataIsLoading = false;
      this.progressAdd()
      //console.log(this.table1);
      
    },
    resetForm(){
      this.loadpercent = 0;
      this.categoryType = {AudioFeature:false,DenseFlow:false,ImageBackground:false,FacialLandmark:false,FacialDescription:false,FeatureAudio:false,FeatureImage:false,FeatureText:false,IsAnalysis:false};
      this.checkCategoryType = {...this.categoryType};
      this.percent = 0;
      this.percentChange = 0;
      this.showTable.tb1 = false;
      this.showTable.tb2 = false;
      this.showTable.tb3 = false;
      this.showTable.tb4 = false;
    },
    progressAdd(){
      //console.log('p:',p);
      const interval = setInterval(() => {
        this.percent += 1;
        if (this.percent >= this.percentChange) {
          clearInterval(interval);
        }
      }, 100); 
    }
    
  }
}
</script>
